/* 全局样式重置 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB',
    'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%) !important;
  color: #222 !important;
  line-height: 1.6;
}

/* 暗黑主题整体背景 */
body {
  background: #18191c !important;
  color: #e6e6e6 !important;
}

.main-layout,
.layout-content,
.ant-layout,
.ant-layout-content {
  background: transparent !important;
  color: #222 !important;
}

/* 卡片、表格、输入框等主要区域暗色 */
.ant-card, .ant-card-bordered {
  background: #fff !important;
  color: #222 !important;
  border-color: #e6e6e6 !important;
}
.ant-table {
  background: #fff !important;
  color: #222 !important;
}
.ant-table-thead > tr > th {
  background: #fff !important;
  color: #444 !important;
  border-bottom: 1px solid #e6e6e6 !important;
}
.ant-table-tbody > tr > td {
  background: #fff !important;
  color: #222 !important;
  border-bottom: 2px solid #222 !important;
}
.ant-table-thead > tr > th {
  background: #fff !important;
  color: #444 !important;
  border-bottom: 2px solid #222 !important;
}
.ant-input, .ant-select-selector, .ant-picker {
  background: #fff !important;
  color: #222 !important;
  border-color: #d9d9d9 !important;
}
.ant-input::placeholder, .ant-select-selection-placeholder, .ant-picker-input > input::placeholder {
  color: #888 !important;
}

/* 菜单暗色 */
.layout-sider, .ant-menu, .ant-menu-light {
  background: #f7f7f7 !important;
  color: #222 !important;
}
.ant-menu-item {
  background: #f7f7f7 !important;
  color: #222 !important;
}
.ant-menu-item-selected {
  background: #e6f7ff !important;
  color: #1890ff !important;
}
.ant-menu-item-selected .anticon,
.ant-menu-item-selected span {
  color: #1890ff !important;
}

/* 按钮暗色适配 */
.ant-btn {
  background: #fff !important;
  color: #222 !important;
  border-color: #d9d9d9 !important;
}
.ant-btn-primary {
  background: #1890ff !important;
  color: #fff !important;
  border-color: #1890ff !important;
}
.ant-btn:hover, .ant-btn:focus {
  background: #e6f7ff !important;
  color: #1890ff !important;
}

/* 其他细节适配 */
.sider-divider {
  background: #e6e6e6 !important;
}

/* 移动端适配 */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
  
  .ant-layout-sider {
    position: fixed !important;
    height: 100vh;
    z-index: 999;
  }
  
  .ant-layout-content {
    margin-left: 0 !important;
    padding: 16px !important;
  }
  
  .ant-table-wrapper {
    overflow-x: auto;
  }
  
  .ant-card {
    margin-bottom: 16px;
  }
}

/* Pi浏览器优化 */
.pi-optimized {
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
}

/* 自定义滚动条 */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* 登录页面样式 */
.login-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.login-card {
  width: 100%;
  max-width: 400px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  overflow: hidden;
  background: #fff !important;
  color: #222 !important;
}

.login-header {
  text-align: center;
  padding: 32px 24px 24px;
  background: white;
}

.login-title {
  font-size: 24px;
  font-weight: 600;
  color: #1890ff;
  margin-bottom: 8px;
}

.login-subtitle {
  color: #666;
  font-size: 14px;
}

/* 主布局样式 */
.main-layout {
  min-height: 100vh;
}

.layout-header {
  background: white;
  padding: 0 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-title {
  font-size: 18px;
  font-weight: 600;
  color: #1890ff;
  margin-left: 16px;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #666;
}

/* 侧边栏样式 */
.layout-sider {
  background: white;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.06);
}

/* 让菜单容器和菜单项宽度自适应内容 */
.layout-sider {
  min-width: unset !important;
  max-width: unset !important;
  width: auto !important;
}
.ant-menu {
  min-width: unset !important;
  max-width: unset !important;
  width: auto !important;
  display: inline-block !important;
}
.ant-menu-item {
  min-width: unset !important;
  max-width: unset !important;
  width: auto !important;
  padding-left: 24px !important;
  padding-right: 16px !important;
  overflow: visible !important;
  white-space: nowrap !important;
  text-overflow: unset !important;
}
.ant-menu-title-content {
  overflow: visible !important;
  white-space: nowrap !important;
  text-overflow: unset !important;
}

/* 删除sider-logo样式 */
.sider-logo {
  display: none !important;
}

.sider-divider {
  width: 1px;
  background: #eee;
  height: 100vh;
  margin: 0;
  z-index: 1;
}

.layout-header .user-info {
  gap: 4px;
}

/* 顶部Header暗黑色适配 */
.layout-header, .ant-layout-header {
  background: #232326 !important;
  color: #e6e6e6 !important;
  box-shadow: none !important;
  border-bottom: 1px solid #333 !important;
}

/* 内容区域样式 */
.layout-content {
  margin-left: 5px !important;
  margin-right: 5px !important;
  padding: 20px !important;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  min-height: calc(100vh - 112px);
}

/* 页面标题样式 */
.page-header {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #f0f0f0;
}

.page-title {
  font-size: 20px;
  font-weight: 600;
  color: #262626;
  margin-bottom: 8px;
}

.page-description {
  color: #666;
  font-size: 14px;
}

/* 统计卡片样式 */
.stats-card {
  text-align: center;
  padding: 24px;
}

.stats-number {
  font-size: 32px;
  font-weight: 600;
  color: #1890ff;
  margin-bottom: 8px;
}

.stats-label {
  color: #666;
  font-size: 14px;
}

/* 状态标签样式 */
.status-tag {
  border-radius: 12px;
  font-size: 12px;
  padding: 2px 8px;
}

/* 操作按钮组样式 */
.action-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

@media (max-width: 576px) {
  .action-buttons {
    flex-direction: column;
  }
  
  .action-buttons .ant-btn {
    width: 100%;
  }
}

/* 表格响应式 */
@media (max-width: 768px) {
  .ant-table {
    font-size: 12px;
  }
  
  .ant-table-thead > tr > th {
    padding: 8px 4px;
  }
  
  .ant-table-tbody > tr > td {
    padding: 8px 4px;
  }
}

/* 加载状态 */
.loading-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100vw;
}

.loading-wrapper:empty::before,
.loading-wrapper .ant-spin:not(:empty)::before {
  content: '';
  display: block;
  width: 240px;
  height: 10px;
  margin: 0 auto 18px auto;
  background: #e6e6e6;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: relative;
}
.loading-wrapper:empty::after,
.loading-wrapper .ant-spin:not(:empty)::after {
  content: '';
  display: block;
  width: 0%;
  height: 10px;
  background: linear-gradient(90deg, #1890ff 0%, #40a9ff 100%);
  border-radius: 5px;
  position: absolute;
  left: 0;
  top: 0;
  animation: progressBarAnim 1.2s linear infinite;
}

/* 空状态 */
.empty-wrapper {
  text-align: center;
  padding: 48px 24px;
  color: #999;
}

/* 错误状态 */
.error-wrapper {
  text-align: center;
  padding: 48px 24px;
}

.error-title {
  font-size: 18px;
  color: #ff4d4f;
  margin-bottom: 16px;
}

.error-description {
  color: #666;
  margin-bottom: 24px;
}

/* 动画效果 */
.fade-in {
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 工具提示样式 */
.tooltip-content {
  max-width: 300px;
  word-wrap: break-word;
}

/* 表单样式优化 */
.form-section {
  margin-bottom: 32px;
}

.form-section-title {
  font-size: 16px;
  font-weight: 600;
  color: #262626;
  margin-bottom: 16px;
  padding-bottom: 8px;
  border-bottom: 1px solid #f0f0f0;
}

/* 搜索栏样式 */
.search-bar {
  margin-bottom: 24px;
  padding: 16px;
  background: #fafafa;
  border-radius: 8px;
}

/* 批量操作栏 */
.batch-actions {
  margin-bottom: 16px;
  padding: 12px 16px;
  background: #e6f7ff;
  border: 1px solid #91d5ff;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.batch-info {
  color: #1890ff;
  font-size: 14px;
}

/* 详情抽屉样式 */
.detail-drawer .ant-drawer-body {
  padding: 0;
}

.detail-section {
  padding: 24px;
  border-bottom: 1px solid #f0f0f0;
}

.detail-section:last-child {
  border-bottom: none;
}

.detail-section-title {
  font-size: 16px;
  font-weight: 600;
  color: #262626;
  margin-bottom: 16px;
}

.detail-item {
  display: flex;
  margin-bottom: 12px;
}

.detail-label {
  width: 100px;
  color: #666;
  font-size: 14px;
}

.detail-value {
  flex: 1;
  color: #262626;
  font-size: 14px;
}

/* 菜单高亮优化 */
.ant-menu-light .ant-menu-item-selected {
  background: #fff0f0 !important;
}
.ant-menu-light .ant-menu-item-selected .anticon,
.ant-menu-light .ant-menu-item-selected span {
  color: #f5222d !important;
}

/* 项目管理表格紧凑行高和分隔线 */
.custom-table-row {
  height: 40px !important;
  border-bottom: 1px solid #f0f0f0 !important;
}
.ant-table-tbody > tr.custom-table-row > td {
  padding-top: 8px !important;
  padding-bottom: 8px !important;
  border-bottom: 1px solid #f0f0f0 !important;
  height: 40px !important;
  line-height: 24px !important;
  vertical-align: middle !important;
}
.ant-table-tbody > tr.custom-table-row {
  height: 40px !important;
}
.ant-table-tbody > tr.custom-table-row .ant-tag {
  margin: 0 !important;
  line-height: 20px !important;
}
.ant-table-tbody > tr.custom-table-row .ant-btn {
  height: 24px !important;
  line-height: 22px !important;
  padding: 0 8px !important;
}

/* 搜索区外部Card容器紧凑高度 */
.search-bar-top .ant-card, .search-card {
  margin-bottom: 4px !important;
  padding-top: 8px !important;
  padding-bottom: 8px !important;
  box-shadow: none !important;
}
.search-bar-top .ant-card-body, .search-card .ant-card-body {
  padding: 8px 12px 8px 12px !important;
}

.admin-info-top {
  background: #fff !important;
  color: #222 !important;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  margin-bottom: 4px;
}
.admin-info-top .ant-avatar {
  margin-right: 6px;
}

/* 修复暗黑主题下输入框背景和边框 */
.ant-input, .ant-input-affix-wrapper, .ant-input-group-addon, .ant-input-group-wrapper, .ant-input-group {
  background: #232326 !important;
  color: #e6e6e6 !important;
  border-color: #444 !important;
  box-shadow: none !important;
}
.ant-input-affix-wrapper-focused, .ant-input:focus {
  border-color: #1890ff !important;
  box-shadow: 0 0 0 2px rgba(24,144,255,0.2) !important;
}

/* 修复输入框外部白色包裹问题 */
.ant-input-group, .ant-input-group-wrapper, .ant-input-group-addon {
  background: #232326 !important;
  border: none !important;
}

/* 管理员删除按钮强制红色样式 */
.admin-delete-btn {
  background-color: #ff4d4f !important;
  border-color: #ff4d4f !important;
  color: #fff !important;
}

.admin-delete-btn:hover,
.admin-delete-btn:focus,
.admin-delete-btn:active {
  background-color: #ff7875 !important;
}

/* 详情模态框紧凑样式 */
.ant-descriptions-small .ant-descriptions-item-label,
.ant-descriptions-small .ant-descriptions-item-content {
  padding: 8px 16px !important;
}
.ant-descriptions-small .ant-descriptions-row {
  border-bottom: 1px solid #f0f0f0;
}
.ant-modal-body {
  padding: 16px !important;
}
.ant-alert {
  padding: 8px 12px !important;
}

.admin-delete-btn.ant-btn-dangerous {
  background-color: #ff4d4f !important;
  border-color: #ff4d4f !important;
  color: #fff !important;
}

.login-card .ant-input, .login-card .ant-input-affix-wrapper {
  background: #fff !important;
  color: #222 !important;
  border-color: #d9d9d9 !important;
}
.login-card .ant-input::placeholder {
  color: #888 !important;
}

.custom-progress-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 0;
}
.progress-bar {
  width: 240px;
  height: 10px;
  background: #e6e6e6;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 18px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}
.progress-inner {
  width: 0%;
  height: 100%;
  background: linear-gradient(90deg, #1890ff 0%, #40a9ff 100%);
  border-radius: 5px;
  animation: progressBarAnim 1.2s linear infinite;
}
@keyframes progressBarAnim {
  0% { width: 0%; }
  60% { width: 90%; }
  100% { width: 100%; }
}
.progress-text {
  color: #1890ff;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
}

.sider-logo, .header-left {
  background: #fff !important;
  color: #222 !important;
}

.layout-sider .ant-menu {
  margin-top: 32px !important;
}

.dashboard-container,
.layout-content,
.main-layout,
.ant-layout,
.ant-layout-content,
body {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%) !important;
}

.ant-input,
.ant-input-affix-wrapper,
.ant-input-search,
.ant-input-group,
.ant-input-group-addon,
.ant-input-group-wrapper {
  background: #fff !important;
  color: #222 !important;
  border-color: #d9d9d9 !important;
}
.ant-input::placeholder,
.ant-input-affix-wrapper .ant-input::placeholder {
  color: #888 !important;
}

.ant-spin,
.ant-spin-dot,
.ant-spin-dot-item {
  display: none !important;
  background: none !important;
  box-shadow: none !important;
}

.ant-table-tbody > tr {
  border-bottom: 2px solid #222 !important;
}
.ant-table-tbody > tr,
.ant-table-tbody > tr > td {
  border-bottom: 1px solid #000 !important;
}

/* 列表和卡片型项目分隔线为黑色 */
.ant-list-item,
.project-manage-container .ant-card,
.project-audit-container .ant-card,
.withdraw-manage-container .ant-card {
  border-bottom: 2px solid #000 !important;
}

/* 防止最后一项多余分隔线 */
.ant-list-item:last-child,
.project-manage-container .ant-card:last-child,
.project-audit-container .ant-card:last-child,
.withdraw-manage-container .ant-card:last-child {
  border-bottom: none !important;
}

/* 缩小用户管理表格每行高度和内边距 */
.user-manage-container .ant-table-tbody > tr > td {
  padding-top: 2px !important;
  padding-bottom: 2px !important;
}
.user-manage-container .ant-table-tbody > tr {
  height: 32px !important;
  min-height: 0 !important;
}

/* 缩小用户管理搜索区容器的上下间距和内边距 */
.user-manage-container .filter-card {
  margin-top: 4px !important;
  margin-bottom: 4px !important;
  padding-top: 4px !important;
  padding-bottom: 4px !important;
  min-height: 0 !important;
  height: 56px !important;
  display: flex;
  align-items: center;
}

.user-manage-container .ant-table-tbody > tr,
.user-manage-container .ant-table-tbody > tr > td {
  border-bottom: 1px solid #000 !important;
}

/* 表头排序箭头紧贴文字 */
.ant-table-column-sorters {
  display: inline-flex !important;
  align-items: center !important;
  gap: 2px !important;
  justify-content: flex-start !important;
}
.ant-table-column-title {
  margin-right: 2px !important;
}

/* 用户详情抽屉居中弹出，宽度适中，高度自适应 */
.ant-drawer.user-detail-drawer .ant-drawer-content-wrapper {
  left: 50% !important;
  transform: translateX(-50%) !important;
  right: auto !important;
  width: 480px !important;
  max-width: 96vw !important;
  top: 32px !important;
  bottom: 32px !important;
  height: auto !important;
  max-height: calc(100vh - 64px) !important;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ant-drawer.user-detail-drawer .ant-drawer-body {
  overflow: visible !important;
  padding-bottom: 16px !important;
}

.project-manage-container .ant-table-tbody > tr,
.project-manage-container .ant-table-tbody > tr > td,
.withdraw-manage-container .ant-table-tbody > tr,
.withdraw-manage-container .ant-table-tbody > tr > td {
  border-bottom: 1px solid #000 !important;
}
.withdraw-table-row-red-border td {
  border-bottom: 2px solid red !important;
  padding-top: 6px !important;
  padding-bottom: 6px !important;
}